	module.exports = {"data":{"markdownRemark":{"frontmatter":{"title":"Privacy policy","items":[{"title":"Revisions to this Privacy Policy.","text":"Information that we collect through our Services is covered by the Privacy Policy in effect at the time such information is collected. We may revise this Privacy Policy from time to time, and will notify you of the revised Privacy Policy by posting it on the Services or by sending you an email or other notification. The “Last Updated Date” above indicates the date this Privacy Policy became effective."},{"title":"Location Information.","text":"When you use our App, we may collect and store information about your location by converting your IP address into a rough geo-location. We may use location information to improve and personalize our Services for you. If you do not want us to collect location information, you may disable that feature on your mobile device."},{"title":"Information that We Share with Third Parties.","text":"Except as explained in this Privacy Policy, we don’t share, sell, trade, rent or otherwise transfer your PII to outside parties. This does not include trusted third parties that assist us in operating our Services, conducting our business or servicing you (e.g. providing you with customer support). These companies are authorized to use your PII only as necessary to provide these services to us. We may also disclose your PII:\n<ul> <li>As required by law, such as to comply with a subpoena or similar legal process; When we believe in good faith that disclosure is necessary to protect our rights, protect your safety or the safety of others or the public in general, or to stop any activity that we consider illegal, unethical or legally actionable; </li> <li> If we are involved in a merger, acquisition or asset sale or if our assets are acquired by a third party in the event we go out of business or enter bankruptcy; </li> <li> To any other third party but only with your prior consent to do so. </li> </ul>\nWe may share aggregated information and non-identifying information with third parties for industry research and analysis, demographic profiling and other similar purposes. We utilize Google Analytics, a service provided by Google, Inc., that we use to gather information about how visitors engage with our Services. For more information about Google Analytics, please visit www.google.com/policies/privacy/partners/."},{"title":"Access and Modification of Your Information.","text":"If you want us to delete your PII and your Account, then access your Account page and follow the process for deleting your Account. We’ll take steps to delete your information as soon we can, but some information may remain in archived/backup copies for our records or as otherwise required by law."},{"title":"Links to Other Sites.","text":"Our Services may contain links to websites and services owned or operated by third parties (each, a “Third-Party Service”). Any information that you provide to a Third-Party Service is subject to the Third-Party Service’s privacy policy, not ours. We’re not responsible for the content, privacy or security practices and policies of any Third-Party Service. To protect your information we recommend that you carefully review the privacy policies of all Third-Party Services that you access."},{"title":"International Transfer.","text":"Your PII may be transferred to, and maintained on, computers located outside of your state, province, country or other governmental jurisdiction where the privacy laws may not be as protective as those in your jurisdiction. If you’re located outside the United States and choose to provide your PII to us, we may transfer your PII to the United States and process it there."},{"title":"Our Policy Toward Children.","text":"Our Services are not directed to children under 13 and we do not knowingly collect PII from children under 13. If we learn that we have collected PII of a child under 13 we will take steps to delete such information from our files as soon as possible."},{"title":"Questions?","text":"Please contact us at hi@saleshero.ai if you have any questions about our Privacy Policy."}]},"html":""}},"pathContext":{"slug":"/privacy/"}}

